.form {
  .avatar-image {
    width: 25%;
    aspect-ratio: 1 / 1;
    position: relative;
    background-color: var(--kt-gray-200);
    border-radius: 6px;

    img {
      position: absolute;
      z-index: 1;
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 6px;
    }

    .btn-avatar-cnt {
      position: absolute;
      z-index: 2;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  .row-2 {
    .column-2 {
      width: calc(50% - 8px);
    }
  }
}